.feedback
   &__grid
      padding: 20px 20px 15px
      background: linear-gradient(#A7C3E7, #C6D8EF, #A7C3E7, #B8CFED, #CAE0FE, #D4E7FF, #B8D7FF)
      border-radius: var(--radius)
      overflow: hidden
      grid-gap: 20px
      position: relative
      overflow: hidden
      // &::after
      //    content: ''
      //    position: absolute
      //    width: 1290px
      //    height: 299px
      //    background: url('../images/elements/lines.svg') no-repeat center
   &__title
      padding-right: 30px
   &__info
      padding: 20px
      color: #fff

   @media screen and (max-width: $width_md)
      &__title
         padding-right: 0

   @media screen and (max-width: $width_xs)
      &__info
         padding: 10px
      &__title
         font-size: 20px

   @media screen and (max-width: $width_mob)
      &__info
         padding: 0

.section
   &--feedback
      margin-bottom: 20px