:root {
  --primary: #111;
  --accent: #2E80EC;
  --gray: #B0B0B0;
  --sectionBg: #F6F6F6;
  --hover-color: #3675bd;
  --radius: 40px;
  --border: 1px solid var(--gray);
  --transition-duration: .2s;
}

.footer__nav a {
  position: relative;
}
.footer__nav a::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: auto;
  right: 0;
  width: 0;
  height: 1px;
  background: var(--primary);
  transition: width var(--transition-duration) ease;
}
.footer__nav a:hover::after, .footer__nav a:focus::after {
  width: 100%;
  left: 0;
  right: auto;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #fff;
  font-family: "Montserrat", "Helvetica", Arial, sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--primary);
  font-size: 16px;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

picture {
  display: block;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.wrapper {
  width: 100%;
  max-width: 1440px;
  min-width: 320px;
  padding: 20px;
  margin: 0 auto;
}

.container {
  padding: 0 36px;
}

@media screen and (max-width: 767px) {
  .wrapper {
    padding: 16px;
  }
  .container {
    padding: 0 26px;
  }
}
@media screen and (max-width: 560px) {
  .container {
    padding: 0 16px;
  }
}
.btn {
  padding: 18px 27px;
  display: inline-block;
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  background: var(--accent);
  border: 1px solid transparent;
  border-radius: 100px;
  color: #fff;
  cursor: pointer;
  transition: background var(--transition-duration) ease, color var(--transition-duration) ease;
}
.btn:hover, .btn:focus {
  background: var(--hover-color);
}
.btn--light {
  background: #fff;
  color: var(--accent);
}
.btn--light:hover, .btn--light:focus {
  background: var(--accent);
  color: #fff;
}
.btn--outline {
  background: #fff;
  border: 1px solid var(--accent);
  color: var(--accent);
}
.btn--outline:hover, .btn--outline:focus {
  color: #fff;
}
.btn--wide {
  width: 100%;
}

.btn-circle {
  width: 55px;
  min-width: 55px;
  height: 55px;
  border-radius: 50%;
  background: #fff;
  color: var(--accent);
  overflow: hidden;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: background var(--transition-duration) ease, color var(--transition-duration) ease;
}
.btn-circle:hover, .btn-circle:focus {
  background: var(--hover-color);
  border-color: var(--hover-color);
  color: #fff;
}

.btn-info {
  padding: 20px;
  display: inline-block;
  border-radius: 100px;
  background: rgba(17, 17, 17, 0.2);
  color: #fff;
  font-size: 12px;
  font-weight: 500;
}

.tel {
  font-size: 18px;
  font-family: Montserrat;
  font-weight: 500;
  white-space: nowrap;
}

.email {
  font-size: 18px;
  font-family: Montserrat;
  font-weight: 500;
  white-space: nowrap;
  color: var(--accent);
  transition: color var(--transition-duration) ease;
}
.email:hover, .email:focus {
  color: var(--hover-color);
}

.title {
  font-size: 28px;
  font-weight: 500;
}
.title--small {
  font-size: 24px;
}
.title--big {
  font-size: 40px;
}

.link {
  padding: 16px 20px;
  border-radius: 12px;
  background: #fff;
  color: var(--accent);
  text-align: center;
  display: inline-block;
  font-size: 18px;
  font-weight: 500;
  transition: background var(--transition-duration) ease, color var(--transition-duration) ease;
}
.link:hover, .link:focus {
  background: var(--hover-color);
  color: #fff;
}
.link--gray {
  background: var(--sectionBg);
}

.text {
  font-size: 16px;
  color: var(--gray);
}

.text-dots {
  padding-left: 32px;
  position: relative;
}
.text-dots--small {
  max-width: 282px;
  font-size: 14px;
}
.text-dots::before {
  content: "";
  position: absolute;
  left: 0;
  top: 5px;
  width: 14px;
  height: 5px;
  background: url("../images/elements/dots.svg") no-repeat center/contain;
}

.img-cover {
  width: 100%;
  padding-bottom: 60%;
  border-radius: var(--radius);
  overflow: hidden;
  position: relative;
}
.img-cover img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.list__item {
  margin-bottom: 32px;
  padding-left: 36px;
  position: relative;
}
.list__item::before {
  content: "\e903";
  font-family: "icomoon";
  speak: none;
  position: absolute;
  left: 0;
  top: 0;
  color: var(--accent);
  font-size: 16px;
}
.list__item:last-child {
  margin-bottom: 0;
}
.list__item p {
  margin-bottom: 14px;
}
.list__item p:last-child {
  margin-bottom: 0;
}
.list__item b {
  font-size: 18px;
  font-weight: 500;
}

.box {
  border-radius: var(--radius);
  background: var(--sectionBg);
}

@media screen and (max-width: 1170px) {
  .title {
    font-size: 24px;
  }
  .title--small {
    font-size: 22px;
  }
}
@media screen and (max-width: 767px) {
  .link {
    font-size: 16px;
  }
  .btn {
    font-size: 14px;
  }
  .title {
    font-size: 22px;
  }
  .title--small {
    font-size: 20px;
  }
  .list__item {
    font-size: 14px;
    line-height: 140%;
  }
  .list__item b {
    font-size: 16px;
  }
  .list__item::before {
    font-size: 14px;
  }
}
.swiper-button-prev, .swiper-button-next {
  width: 52px;
  height: 52px;
  margin: 0;
  border-radius: 50%;
  background: #fff;
  color: var(--accent);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: static;
  transition: background var(--transition-duration) ease, color var(--transition-duration) ease;
}
.swiper-button-prev:hover, .swiper-button-prev:focus, .swiper-button-next:hover, .swiper-button-next:focus {
  background: var(--hover-color);
  color: #fff;
}
.swiper-button-prev::after, .swiper-button-next::after {
  font-size: 12px;
  font-weight: bold;
}

.animation {
  transition: all 0.3s ease-out;
}

.menu {
  display: flex;
  align-items: center;
}
.menu__burger {
  margin-right: 8px;
  color: var(--primary);
}
.menu__list {
  display: flex;
  align-items: center;
}
.menu__item:not(:first-child):not(:last-child) .btn {
  border-radius: 0;
}
.menu__item:first-child .btn {
  padding: 17px 14px 17px 24px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.menu__item:last-child .btn {
  padding: 17px 24px 17px 14px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.menu__link {
  min-height: 55px;
  padding: 17px 12px;
  color: var(--primary);
  font-size: 12px;
  background: rgba(255, 255, 255, 0.6);
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.menu__manage {
  margin-left: auto;
}
.menu__manage-btn {
  font-size: 14px;
}
@media screen and (max-width: 767px) {
  .menu__nav {
    display: none;
  }
}

.header--study .hero {
  max-width: 910px;
}
.hero__inner {
  padding: 20px;
}
.hero__title {
  font-weight: 400;
  font-size: 84px;
}
.hero__title-colour {
  display: block;
  color: var(--accent);
}
.hero__title-right {
  padding-right: 26px;
  text-align: right;
  display: block;
}
.hero__title-pic {
  position: relative;
}
.hero__title-pic br {
  display: none;
}
.hero__title-logo {
  display: inline-block;
  width: 95px;
  position: absolute;
  left: calc(100% + 20px);
  top: 0;
  transform: translateX(80px);
}
.hero__title-logo.animated {
  transform: translateX(0);
}
.hero__cite {
  width: 100%;
  max-width: 417px;
  margin-top: -66px;
  font-size: 24px;
  line-height: 120%;
  font-weight: 500;
  transform: translateY(80px);
}
.hero__cite.animated {
  transform: translateY(0);
}
.hero__lead {
  width: 100%;
  max-width: 585px;
  font-size: 18px;
  line-height: 140%;
}
.hero__bottom {
  margin-top: 166px;
  display: flex;
  justify-content: space-between;
}
.hero__bottom-text {
  max-width: 518px;
  font-size: 18px;
  line-height: 155%;
}
.hero__bottom-text a {
  font-weight: 700;
  color: var(--accent);
}
.hero__bottom-content {
  width: 635px;
  position: relative;
}
.hero__bottom-img {
  width: 100%;
  position: absolute;
  top: -160px;
  right: 0;
  transform: rotate(0) translateX(180px) translateY(0);
}
.hero__bottom-img.animated {
  transform: rotate(0) translateX(0) translateY(0);
}
.hero__description {
  margin-top: 34px;
  max-width: 724px;
  font-size: 18px;
  line-height: 144%;
}
.header--patients .hero__top {
  max-width: 800px;
}
.header--patients .hero__title {
  margin-bottom: 10px;
  font-size: 120px;
}
@media screen and (max-width: 1600px) {
  .hero__bottom-img {
    transform: translateY(50px);
  }
}
@media screen and (max-width: 1365px) {
  .hero__title {
    margin-bottom: 20px;
    font-size: 68px;
  }
  .hero__title-colour {
    padding-right: 60px;
  }
  .hero__cite {
    margin-top: 0;
  }
  .header--patients .hero__title {
    font-size: 100px;
  }
}
@media screen and (max-width: 1170px) {
  .hero__title {
    font-size: 60px;
  }
  .hero__title-pic br {
    display: inline;
  }
  .hero__title-logo {
    left: 240px;
