    padding-right: 0;
  }
  .hero__bottom {
    margin-top: 100px;
  }
  .hero__bottom-content {
    width: 500px;
    flex-shrink: 0;
  }
  .hero__bottom-img {
    top: -90px;
  }
  .hero__description {
