  }
  .header--patients .hero__title {
    font-size: 80px;
  }
}
@media screen and (max-width: 991px) {
  .header--patients .hero__title {
    font-size: 60px;
  }
  .hero__title {
    font-size: 54px;
  }
  .hero__title-logo {
    left: 200px;
    top: -20px;
  }
  .hero__bottom {
    flex-direction: column;
  }
  .hero__bottom-text {
    max-width: 100%;
  }
  .hero__bottom-content {
    width: 100%;
    max-width: 100%;
    height: 150px;
  }
  .hero__bottom-img {
    width: 100%;
