  }
  .hero__bottom-img.animated {
    transform: translateX(-50%) translateY(0);
  }
  .hero__lead {
    font-size: 16px;
  }
  .hero__cite {
    transform: translateY(30px);
  }
}
@media screen and (max-width: 767px) {
  .hero__inner {
    padding: 8px;
  }
  .hero__title {
    font-size: 48px;
  }
  .hero__cite {
    font-size: 18px;
  }
