}
@media screen and (max-width: 400px) {
  .hero__title {
    margin-bottom: 28px;
  }
  .header--index .hero__title-colour {
    text-align: right;
    padding-left: 0;
    font-size: 27px;
  }
  .hero__bottom {
    margin-top: 42px;
  }
  .hero__bottom-text {
    line-height: 175%;
  }
