    height: 56px;
  }
  .hero__bottom-img {
    top: -15px;
    left: 56%;
  }
}

.header {
  margin-bottom: 100px;
}
.header--index {
  margin-bottom: 35px;
}
.header--study {
  margin-bottom: 70px;
}
.header--patients {
  color: #fff;
}
.header--patients .header__inner {
  background: linear-gradient(#A7C3E7, #CAE0FE, #D4E7FF, #B8D7FF);
}
.header__inner {
  min-height: 781px;
  padding: 16px;
  background: rgba(211, 211, 211, 0.5);
  border-radius: var(--radius);
  position: relative;
}
.header--index .header__inner {
  min-height: 662px;
  background-image: url("../images/header/index-fon.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.header__menu {
  margin-bottom: 35px;
}
.header__natalya {
  width: 414px;
  position: absolute;
  bottom: 0;
  right: 58px;
  display: flex;
  justify-content: flex-end;
  transform: translateX(30px);
}
.header__natalya.animated {
  transform: translateX(0);
}
.header__natalya img {
  width: 414px;
  height: auto;
}
.header__logo {
  width: 154px;
  position: absolute;
  bottom: 36px;
  left: 36px;
  opacity: 0;
  transition: transform 0.2s ease;
}
.header__logo.animated {
  opacity: 1;
}
.header__eye {
  width: 585px;
  position: absolute;
  right: 77px;
